.App {
  text-align: center;
  min-height: 100vh;
  padding: 20px 0;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

svg {
  display: block;
  margin: 0 auto;
}

.tooltip {
  font-size: 12px;
  font-weight: bold;
  pointer-events: none;
  fill: #424242;
}

path {
  transition: fill 0.3s ease;
}

path:hover {
  cursor: pointer;
}

circle {
  transition: all 0.3s ease;
}

circle:hover {
  cursor: pointer;
  stroke: #000;
  stroke-width: 2px;
}

.correlation {
  font-family: monospace;
}

.value-label {
  font-size: 12px;
  fill: #424242;
}

.area {
  transition: opacity 0.3s ease;
}

.area:hover {
  opacity: 0.8;
}

.dot:hover {
  r: 6;
}

@media (max-width: 960px) {
  .App {
    padding: 10px;
  }
  
  svg {
    max-width: 100%;
    height: auto;
  }
}
